<template>
  <za-page-content header="爬取图文审核" scroll="true">
    <za-table ref="table" :data="articles" :total="totalArticleNum" @action-change="changePage" @selection-change="handleSelectionChange" :isLoading="loadingTable">
      <el-col :span="24" style="padding-bottom: 0px;" slot="searchBar">
        <el-form :model="filters" ref="filtersForm">
          <el-form-item>
            <el-col :span="8">
              <el-col :span="8">来源:</el-col>
              <el-col :span="16">
                <el-select style="width:100%" v-model="filters.origin" placeholder="请选择">
                  <el-option v-for="item in origins" :key="item" :label="item" :value="item">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>

            <el-col :span="8" :offset="4">
              <el-col :span="8">所属栏目:</el-col>
              <el-col :span="16">
                <el-select style="width:100%" v-model="filters.directoryId" placeholder="请选择">
                  <el-option v-for="item in colunm" :key="item.directoryId" :label="item.directoryName" :value="item.id">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>
          </el-form-item>

          <el-form-item>
            <el-col :span="8">
              <el-col :span="8">标题:</el-col>
              <el-col :span="16">
                <el-input v-model="filters.title" @keyup.native.enter="filter" placeholder="搜索标题..."></el-input>
              </el-col>
            </el-col>

            <el-col :span="8" :offset="4">
              <el-col :span="8">发布时间:</el-col>
              <el-col :span="16">
                <el-date-picker v-model="filters.timeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss"
                  start-placeholder="开始时间" end-placeholder="结束时间">
                </el-date-picker>
              </el-col>
            </el-col>
          </el-form-item>

          <el-form-item>
            <el-col :span='10'>
              <el-button icon="el-icon-search" type="primary" @click="filter">查询</el-button>
              <el-button type="success" @click="handlePass">批量通过</el-button>
              <el-button type="danger" @click="handleDelete">批量删除</el-button>
            </el-col>
          </el-form-item>
        </el-form>
      </el-col>

      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="title" label="标题"></el-table-column>
      <el-table-column prop="origin" label="来源"></el-table-column>
      <el-table-column prop="requestUrl" label="来源链接">
        <template slot-scope="scope">
          <a :href="scope.row.requestUrl" target="_Blank">{{scope.row.requestUrl}}</a>
        </template>
      </el-table-column>
      <el-table-column prop="directoryName" label="所属栏目" width="120"></el-table-column>
      <el-table-column prop="crawlerTime" width="160" :formatter="formatterDate" label="爬取时间"></el-table-column>
      <el-table-column prop="publishTime" width="160" :formatter="formatterDate" label="发布时间"></el-table-column>
      <el-table-column label="操作" fixed="right" width="140">
        <template slot-scope="scope">
          <el-button size="mini" type="success" @click="handlePass(scope.$index, scope.row)">通过</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </za-table>
  </za-page-content>
</template>

<script>
import formatterDate from "../../filters/date/index.js";
import { ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";

export default {
  data() {
    return {
      selectionRowData: [],
      articles: [],
      totalArticleNum: 1,
      filters: {
        directoryId: "",
        origin: "",
        title: "",
        timeRange: null,
      },
      colunm: [],
      origins: [],
      loadingTable: false,
      pageConfig: {
        pageNum: 1,
        pageSize: 10,
        directoryId: "",
        origin: "",
        title: "",
        publishTimeStart: "",
        publishTimeEnd: ""
      }
    };
  },
  created() {
    ajaxPostCommon(ajaxUrl.getListInfoDirectory).then(res => {
      this.colunm = res.result;
      this.colunm.unshift({
        directoryName: "全部"
      });
    });

    ajaxPostCommon(ajaxUrl.getListOrigin).then(res => {
      this.origins = res.result;
      this.origins.unshift("全部");
    });
    this.initData();
  },
  methods: {
    filter() {
      const publishTimeStart = !!this.filters.timeRange ? this.filters.timeRange[0] : '';
      const publishTimeEnd = !!this.filters.timeRange ? this.filters.timeRange[1] : '';
      this.pageConfig = {
        ...this.pageConfig,
        ...this.filters,
        publishTimeStart,
        publishTimeEnd,
        pageNum: 1
      };
      delete this.pageConfig.timeRange;
      if (!this.$refs.table.resetCurrentPage()) {
        this.initData();
      }
    },

    initData() {
      this.loadingTable = true;
      ajaxPostCommon(ajaxUrl.getInfoList, {
        ...this.pageConfig
      }).then(res => {
        this.articles = res.result.list;
        this.totalArticleNum = res.result.total;
        this.loadingTable = false;
      });
    },

    formatterDate(r, c, v) {
      return formatterDate(v);
    },

    // 通过文章
    handlePass(i, r) {
      let articleIds = !!r ? r.id : this.parseSelected();
      if (!articleIds) {
        this.$message({
          showClose: true,
          type: "error",
          message: "请选择文章"
        });
        return false;
      }
      this.$confirm("确定通过审核么?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.passUncheckedArticle, {
            list: articleIds
          }).then(res => {
            if (res.returnCode == 200) {
              this.$message({
                showClose: true,
                message: "通过!",
                type: "success"
              });
              this.initData();
            }
          });
        })
        .catch(() => {});
    },

    // 删除文章
    handleDelete(i, r) {
      let articleIds = !!r ? r.id : this.parseSelected();
      if (!articleIds) {
        this.$message({
          showClose: true,
          type: "error",
          message: "请选择文章"
        });
        return false;
      }
      this.$confirm("确定删除该资讯么?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.deleteUncheckedArticle, {
            list: articleIds
          }).then(res => {
            this.$message({
              showClose: true,
              message: "删除成功!",
              type: "success"
            });
            this.initData();
          });
        })
        .catch(() => {});
    },

    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },

    parseSelected() {
      let articleArray = [];
      this.selectionRowData.forEach(i => {
        articleArray.push(i.id);
      });
      return articleArray.join(",");
    },

    changePage(params) {
      let queryParam = {
        pageNum: params.page,
        pageSize: params.rows || this.pageConfig.pageSize
      };
      this.pageConfig = { ...this.pageConfig, ...queryParam };
      this.initData();
    }
  }
};
</script>

<style lang="scss" scoped>
.pageagation {
  text-align: right;
}
.el-date-editor.el-input {
  width: auto;
}
.el-button + .el-button {
  margin-left: 0;
  margin-top: 4px;
}
</style>



